import React from "react"
import {
  Button,
  createStyles,
  Grid,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core"
import { Link } from "react-router-dom"
import { makeStyles } from "@material-ui/core/styles"
import { Add as AddIcon } from "@material-ui/icons"

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    createButtonWrap: {
      textAlign: "right",
      marginBottom: "16px",
    },
    table: {
      minWidth: 650,
    },
    button: {
      margin: theme.spacing(1),
    },
  })
)

function createData(eventName: string, description: string, createdAt: string) {
  return { eventName, description, createdAt }
}

const rows = [
  createData("Payment Complete", "Event when finishing payment procedure", "2020-5-31"),
  createData("Try CheckOut", "Event that clicking ‘CheckOut’ button", "2020-4-23"),
  createData("Cart PV", "Entering Cart Page", "2020-4-23"),
]

export default function Event() {
  const classes = useStyles()
  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs={9}>
          <Typography variant={"h6"}>Event</Typography>
          <Typography variant={"caption"}>Create a event for tracking path of customers</Typography>
        </Grid>
        <Grid item xs={3}>
          <div className={classes.createButtonWrap}>
            <Button
              variant="contained"
              color="primary"
              className={classes.button}
              startIcon={<AddIcon />}
              component={Link}
              to={`/events/new`}
            >
              New
            </Button>
          </div>
        </Grid>
      </Grid>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell align="center">Created</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.eventName}>
                <TableCell>
                  {row.eventName}
                  <br />
                  <span>{row.description}</span>
                </TableCell>
                <TableCell align="center">{row.createdAt}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}
