import React from "react"
import "./App.css"
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom"
import { IntlProvider } from "react-intl"
import { en } from "./locale"
import pages, { login } from "./pages"
import MainFrame from "./layouts/MainFrame"
import SuccessSnackbar from "./components/Snackbar/SuccessSnackbar"
import { snackbarStates } from "./states/Feedback/Snackbar"
import { useRecoilState } from "recoil/dist"

const defaultLang = "en"
let authenticated: boolean | null = true

function App() {
  const [snackbar, setSnackbar] = useRecoilState(snackbarStates)
  const handleSnackBarClose = () => setSnackbar({ message: "", open: false })
  return (
    <IntlProvider locale={defaultLang} messages={en}>
      <Router>
        <Switch>
          {authenticated === null ? (
            <div>인증 확인중</div>
          ) : authenticated ? (
            <>
              {Object.values(pages).map((page, i) => {
                return (
                  <Route
                    path={page.path}
                    exact={page.exact}
                    render={(props) => {
                      const Component = page.component
                      return (
                        <MainFrame {...props}>
                          <Component {...props} />
                        </MainFrame>
                      )
                    }}
                    key={i}
                  />
                )
              })}
              <Route path={login.path} exact={login.exact} component={login.component} />
            </>
          ) : (
            <>
              <Route path={"/login"} render={login.component} />
              <Redirect path="*" to="/" />
            </>
          )}
        </Switch>
      </Router>
      <SuccessSnackbar open={snackbar.open} message={snackbar.message} onClose={handleSnackBarClose} />
    </IntlProvider>
  )
}

export default App
