import React from "react"
import {
  Button,
  Grid,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"
import { Link, RouteComponentProps } from "react-router-dom"
import { Add as AddIcon } from "@material-ui/icons"
import ExperimentState from "./ExperimentState"
import { ExperimentStatusType } from "../../model/ExperimentStatusType"

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  createButtonWrap: {
    textAlign: "right",
    marginBottom: "16px",
  },
  button: {
    margin: theme.spacing(1),
  },
  table: {
    minWidth: 650,
  },
}))

function createData(
  name: string,
  key: string,
  trafficAllocation: number,
  trafficGroup: string,
  status: ExperimentStatusType,
  createdAt: string
) {
  return { name, key, trafficAllocation, trafficGroup, status, createdAt }
}

const rows = [
  createData("Test Name Example", "5521", 30, "50/50", "READY", "2020-5-31"),
  createData("[2020.Q3] SRP 개편 2차 테스트", "1382", 60, "34/33/33", "RUNNING", "2020-5-31"),
  createData("[2020.Q3] SRP 개편 3차 테스트", "4431", 60, "34/33/33", "PAUSED", "2020-5-31"),
  createData("[2020.Q3] SRP 개편 4차 테스트", "4443", 60, "34/33/33", "STOPPED", "2020-5-31"),
]
export default function Experiment(_props: RouteComponentProps) {
  const classes = useStyles()
  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs={9}>
          <Typography variant={"h6"}>A/B Test</Typography>
          <Typography variant={"caption"}>Create an a/b test for product experimentation</Typography>
        </Grid>
        <Grid item xs={3}>
          <div className={classes.createButtonWrap}>
            <Button
              variant="contained"
              color="primary"
              className={classes.button}
              startIcon={<AddIcon />}
              component={Link}
              to={"/experiments/new"}
            >
              New
            </Button>
          </div>
        </Grid>
      </Grid>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell width={"60%"}>Name</TableCell>
              <TableCell align="center">Status</TableCell>
              <TableCell align="center">Created</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.name} hover>
                <TableCell component="th" scope="row">
                  <Link to={`experiments/detail/${row.key}`}>{row.name}</Link>
                  <br />({`#${row.key}`})
                </TableCell>
                <TableCell align="center">
                  <ExperimentState type={row.status}>{row.status}</ExperimentState>
                </TableCell>
                <TableCell align="center">{row.createdAt}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}
