import React, { useState } from "react"
import { FormattedMessage } from "react-intl"
import { AppBar, createStyles, IconButton, Menu, MenuItem, Toolbar } from "@material-ui/core"
import { AccountCircle } from "@material-ui/icons"
import { makeStyles } from "@material-ui/core/styles"

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      display: "flex",
    },
    grow: {
      flexGrow: 1,
    },
    sectionDesktop: {
      [theme.breakpoints.up("md")]: {
        display: "flex",
      },
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
  })
)

export default function Header() {
  const classes = useStyles()
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const isMenuOpen = Boolean(anchorEl)
  const handleProfileMenuOpen = (e: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(e.currentTarget)
  }
  const handleMenuClose = () => {
    setAnchorEl(null)
  }

  return (
    <AppBar color={"inherit"} position="fixed">
      <Toolbar>
        <div className={classes.grow} />
        <IconButton edge="end" aria-label="account of current user" color="inherit" onClick={handleProfileMenuOpen}>
          <AccountCircle />
        </IconButton>
      </Toolbar>
      <Menu
        anchorEl={anchorEl}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
        id={"primary-search-account-menu"}
        keepMounted
        transformOrigin={{ vertical: "top", horizontal: "right" }}
        open={isMenuOpen}
        onClose={handleMenuClose}
      >
        {/*<MenuItem onClick={handleMenuClose} component={Link} to={"/account"}>*/}
        {/*  <FormattedMessage id="account" />*/}
        {/*</MenuItem>*/}
        <MenuItem onClick={handleMenuClose}>
          <FormattedMessage id="logout" />
        </MenuItem>
      </Menu>
    </AppBar>
  )
}
