import React, { useState } from "react"
import { RouteComponentProps } from "react-router-dom"
import { createStyles, makeStyles, withStyles } from "@material-ui/core/styles"
import { Button, Grid, IconButton, Paper, Tab, Tabs, Typography } from "@material-ui/core"
import {
  Archive,
  PauseCircleOutline,
  PlayArrow,
  PlayCircleOutline,
  RemoveCircleOutline,
  Replay,
  SkipNext,
  SkipNextOutlined,
  Stop,
} from "@material-ui/icons"
import { ExperimentStatusType } from "../../model/ExperimentStatusType"
import EmptyDivider from "../../components/Divider/EmptyDivider"
import VariationTab from "./tabs/VariationTab"
import ExperimentState from "./ExperimentState"

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary,
    },
    buttonGroup: {
      textAlign: "right",
    },
    button: {
      margin: theme.spacing(1),
    },
    start: {
      padding: 0,
      marginLeft: 8,
      marginRight: 8,
      color: theme.palette.success.main,
      "&:hover": {
        color: theme.palette.success.dark,
      },
    },
    pause: {
      padding: 0,
      marginLeft: 8,
      marginRight: 8,
    },
  })
)

const AntTabs = withStyles({
  root: {
    borderBottom: "1px solid #e8e8e8",
  },
  indicator: {
    backgroundColor: "#1890ff",
  },
})(Tabs)

interface StyledTabProps {
  label: string
}

const AntTab = withStyles((theme) =>
  createStyles({
    root: {
      textTransform: "none",
      minWidth: 72,
      fontWeight: theme.typography.fontWeightRegular,
      marginRight: theme.spacing(4),
      fontFamily: [
        "-apple-system",
        "BlinkMacSystemFont",
        '"Segoe UI"',
        "Roboto",
        '"Helvetica Neue"',
        "Arial",
        "sans-serif",
        '"Apple Color Emoji"',
        '"Segoe UI Emoji"',
        '"Segoe UI Symbol"',
      ].join(","),
      "&:hover": {
        color: "#40a9ff",
        opacity: 1,
      },
      "&$selected": {
        color: "#1890ff",
        fontWeight: theme.typography.fontWeightMedium,
      },
      "&:focus": {
        color: "#40a9ff",
      },
    },
    selected: {},
  })
)((props: StyledTabProps) => <Tab disableRipple {...props} />)

interface Props {
  key: string
}

export default function ExperimentDetail({ match }: RouteComponentProps<Props>) {
  const [status, setStatus] = useState<ExperimentStatusType>("READY")
  const [value, setValue] = React.useState(2)

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue)
  }

  const start = () => setStatus("RUNNING")
  const pause = () => setStatus("PAUSED")

  const statusButton = () => {
    switch (status) {
      case "READY":
        return (
          <IconButton edge="end" aria-label="start" color="primary" onClick={start} className={classes.start}>
            <PlayCircleOutline />
          </IconButton>
        )
      case "RUNNING":
        return (
          <IconButton edge="end" aria-label="pause" color="secondary" onClick={pause} className={classes.pause}>
            <PauseCircleOutline />
          </IconButton>
        )
      case "PAUSED":
        return (
          <IconButton edge="end" aria-label="pause" color="primary" onClick={start} className={classes.start}>
            <SkipNext />
          </IconButton>
        )
    }
  }
  const classes = useStyles()
  const { params } = match
  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Grid container spacing={3}>
              <Grid item xs={7}>
                <Typography variant={"subtitle1"}>{`#${params.key}`}</Typography>
                <Typography
                  suppressContentEditableWarning={true}
                  variant={"h5"}
                  contentEditable={true}
                  onBlur={(e) => {
                    //TODO
                    console.log(e.target.textContent)
                  }}
                >
                  Recommendation logic v2
                </Typography>
                <Typography
                  suppressContentEditableWarning={true}
                  variant={"subtitle1"}
                  contentEditable={true}
                  onBlur={(e) => {
                    //TODO
                    console.log(e.target.textContent)
                  }}
                >
                  A/B Test Description is
                </Typography>
              </Grid>
              <Grid item xs={5}>
                <div className={classes.buttonGroup}>
                  <Button
                    variant="contained"
                    color="default"
                    className={classes.button}
                    startIcon={<Stop />}
                    onClick={() => {}}
                  >
                    Complete
                  </Button>
                  <Button
                    variant="contained"
                    color="default"
                    className={classes.button}
                    startIcon={<Archive />}
                    onClick={() => {}}
                  >
                    Archive
                  </Button>
                </div>
              </Grid>
              <Grid item xs={8}>
                <Grid container spacing={3}>
                  <Grid item xs={3}>
                    <Typography variant={"subtitle2"}>Status</Typography>
                  </Grid>
                  <Grid item xs={9}>
                    <ExperimentState type={status} />
                    {statusButton()}
                  </Grid>
                  <Grid item xs={3}>
                    <Typography variant={"subtitle2"}>Traffic Allocation</Typography>
                  </Grid>
                  <Grid item xs={9}>
                    0%
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={4} />
              <Grid item xs={8}></Grid>
              <Grid item xs={4} />
            </Grid>
            <EmptyDivider />
            <AntTabs value={value} onChange={handleChange} aria-label="ant example">
              <AntTab label="Metric" />
              <AntTab label="Allowlist" />
              <AntTab label="Variation" />
            </AntTabs>
            {value === 0 && <div>Metric</div>}
            {value === 1 && <div>Allowlist</div>}
            {value === 2 && <VariationTab />}
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}
