import React, { useState } from "react"
import {
  Grid,
  IconButton,
  Input,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@material-ui/core"
import { AddCircleOutline, RemoveCircleOutline } from "@material-ui/icons"
import { createStyles, makeStyles } from "@material-ui/core/styles"

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    divider: {
      marginBottom: "16px",
    },
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary,
    },
    buttonGroup: {
      textAlign: "right",
    },
    button: {
      margin: theme.spacing(1),
    },
    margin: {
      margin: theme.spacing(1),
    },
    marginR: {
      marginLeft: theme.spacing(0.5),
      marginRight: theme.spacing(0.5),
    },
    textField: {
      width: "25ch",
    },
    table: {
      minWidth: "650px",
    },
    gray: {
      color: theme.palette.grey.A200,
    },
    distributionInput: {
      textAlign: "right",
    },
  })
)

interface Props {}

const variationKeys = ["A", "B", "C", "D", "E", "F", "G", "H", "I", "J"]

export default function ExperimentVariation(props: Props) {
  const classes = useStyles()

  const [variations, setVariations] = useState([
    { variation: "A", distribution: "50" },
    { variation: "B", distribution: "50" },
  ])

  const addVariation = () => {
    setVariations((old) => {
      if (old.length >= variationKeys.length) return old
      const newVariations = old.map((o) => {
        return {
          ...o,
          distribution: getReBalanceDistribution(+1),
        }
      })
      return [...newVariations, { variation: getNextVariationKey(), distribution: getReBalanceDistribution(+1) }]
    })
  }
  const getNextVariationKey = (): string => {
    const foundKey = variationKeys.find((key) => {
      return !variations.some((v) => v.variation === key)
    })
    return foundKey!!
  }

  const getReBalanceDistribution = (balancing: number) => (100 / (variations.length + balancing)).toFixed(2)

  const removeVariation = (variationKey: string) => {
    const filtered = variations.filter((v) => v.variation !== variationKey)
    const newVariation = filtered.map((f) => {
      return { ...f, distribution: getReBalanceDistribution(-1) }
    })
    setVariations(newVariation)
  }

  return (
    <>
      <TableContainer>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell width={"10%"} align="center">
                Variation
              </TableCell>
              <TableCell align="center">
                Description <span className={classes.gray}>(Optional)</span>
              </TableCell>
              <TableCell align="center">Distribution</TableCell>
              <TableCell align="center" />
            </TableRow>
          </TableHead>
          <TableBody>
            {variations.map((row) => (
              <TableRow key={row.variation}>
                <TableCell align={"center"}>{row.variation}</TableCell>
                <TableCell>
                  <Input fullWidth />
                </TableCell>
                <TableCell width={"20%"}>
                  <Input value={row.distribution} style={{ textAlign: "right" }} endAdornment={"%"} disabled={true} />
                </TableCell>
                <TableCell width={"10%"}>
                  {row.variation !== "A" && variations.length >= 3 && (
                    <IconButton
                      edge="end"
                      aria-label="remove"
                      color="secondary"
                      onClick={() => removeVariation(row.variation)}
                    >
                      <RemoveCircleOutline />
                    </IconButton>
                  )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      {variations.length < variationKeys.length && (
        <IconButton edge="end" aria-label="remove" color="primary" onClick={addVariation}>
          <AddCircleOutline />
        </IconButton>
      )}
    </>
  )
}
