import { atom } from "recoil"
export type EnvType = "production" | "development"
let envProfile = localStorage.getItem("envProfile") as EnvType
if (!envProfile) {
  localStorage.setItem("envProfile", "production")
  envProfile = "production"
}
export const envProfileState = atom<EnvType>({
  key: "envProfile",
  default: envProfile,
})
