import React from "react"
import {
  LiveTv as LiveTvIcon,
  Person as PersonIcon,
  DeveloperMode as DeveloperModeIcon,
  Event as EventIcon,
  Settings as SettingIcon,
  Assignment as AssignmentIcon,
} from "@material-ui/icons"
import Experiment from "./Experiment/Experiment"
import Account from "./Account/Account"
import Event from "./Event/Event"
import Setting from "./Setting/Setting"
import Tutorial from "./Tutorial/Tutorial"
import Developer from "./Developer/Developer"
import ExperimentDetail from "./Experiment/ExperimentDetail"
import Login from "./Login/Login"
import Home from "./Home/Home"
import ExperimentsCreation from "./Experiment/ExperimentsCreation"
import EventCreation from "./Event/EventCreation"

export class RoutingComponent<T> {
  text: string
  path: string
  component: T
  componentIcon: React.ComponentType
  exact: boolean

  constructor(text: string, path: string, component: T, componentIcon: React.ComponentType, exact = false) {
    this.text = text
    this.path = path
    this.component = component
    this.componentIcon = componentIcon
    this.exact = exact
  }
}

export interface RoutingComponentMap<T> {
  [key: string]: RoutingComponent<T>
}
const routingComponent = {
  home: new RoutingComponent("Experiment", "/", Home, AssignmentIcon, true),
  experiment: new RoutingComponent("A/B Test", "/experiments", Experiment, AssignmentIcon, true),
  experimentNew: new RoutingComponent(
    "Create new A/B Test",
    "/experiments/new",
    ExperimentsCreation,
    AssignmentIcon,
    true
  ),
  experimentDetail: new RoutingComponent(
    "ExperimentDetail",
    "/experiments/detail/:key",
    ExperimentDetail,
    AssignmentIcon
  ),
  event: new RoutingComponent("Event", "/events", Event, EventIcon, true),
  eventNew: new RoutingComponent("Create new event", "/events/new", EventCreation, EventIcon, true),
  setting: new RoutingComponent("Setting", "/settings", Setting, SettingIcon),
  account: new RoutingComponent("Account", "/accounts", Account, PersonIcon),
  tutorial: new RoutingComponent("Tutorial", "/tutorials", Tutorial, LiveTvIcon),
  developers: new RoutingComponent("Developers", "/developers", Developer, DeveloperModeIcon),
}
export const login = new RoutingComponent("Login", "/login", Login, DeveloperModeIcon)
export default routingComponent
