import React, { ChangeEvent, useState } from "react"
import { Button, Chip, Grid, Paper, TextField, Typography } from "@material-ui/core"
import { createStyles, makeStyles } from "@material-ui/core/styles"
import { RouteComponentProps } from "react-router-dom"
import EmptyDivider from "../../components/Divider/EmptyDivider"

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary,
    },
    divider: {
      marginBottom: "16px",
    },
    buttonGroup: {
      textAlign: "right",
    },
    button: {
      margin: theme.spacing(1),
    },
    margin: {
      margin: theme.spacing(1),
    },
    marginR: {
      marginRight: theme.spacing(0.5),
    },
    textField: {
      width: "25ch",
    },
  })
)

const nameMaxLength = 50
const descriptionMaxLength = 200
export default function EventCreation(props: RouteComponentProps) {
  const classes = useStyles()
  const [name, setName] = useState("")
  const [description, setDescription] = useState("")
  const changeName = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value
    if (nameMaxLength < value.length) return
    setName(value)
  }
  const changeDescription = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value
    if (descriptionMaxLength < value.length) return
    setDescription(value)
  }
  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs={12}>
          <Typography variant={"h6"}>Create new event</Typography>
          <Typography variant={"caption"}>
            <Chip size={"small"} className={classes.marginR} label="Production" variant={"outlined"} color="primary" />
            <Chip size={"small"} className={classes.marginR} label="Development" variant={"outlined"} color="default" />
          </Typography>
        </Grid>
      </Grid>
      <div className={classes.divider} />
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Grid container spacing={3}>
              <Grid item xs={9}>
                <TextField
                  label="Name"
                  style={{ margin: 8 }}
                  helperText={`${name.length}/128`}
                  fullWidth
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  value={name}
                  required={true}
                  autoFocus={true}
                  placeholder={"Proceed to payment"}
                  onChange={changeName}
                />
                <TextField
                  label="Description (Optional)"
                  style={{ margin: 8 }}
                  helperText={`${description.length}/512`}
                  fullWidth
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  value={description}
                  placeholder={"Click 'payment' button"}
                  onChange={changeDescription}
                />
              </Grid>
            </Grid>
            <EmptyDivider />
            <div className={classes.buttonGroup}>
              <Button
                variant="contained"
                color="default"
                className={classes.button}
                onClick={() => {
                  // @ts-ignore
                  props.history.goBack()
                }}
              >
                Cancel
              </Button>
              <Button variant="contained" color="primary" className={classes.button}>
                Create
              </Button>
            </div>
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}
