import React, { PropsWithChildren } from "react"
import { Chip, createStyles } from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"
import { ExperimentStatusType } from "../../model/ExperimentStatusType"

interface Props {
  type: ExperimentStatusType
}

export default function ExperimentState({ type }: PropsWithChildren<Props>) {
  switch (type) {
    case "READY":
      return <ExperimentStateReady />
    case "RUNNING":
      return <ExperimentStateRunning />
    case "PAUSED":
      return <ExperimentStatePaused />
    case "STOPPED":
      return <ExperimentStateStopped />
  }
}

const useStyles = makeStyles((theme) =>
  createStyles({
    status: {
      ...theme.typography.button,
      width: "110px",
      padding: theme.spacing(1),
      textAlign: "center",
      color: theme.palette.common.white,
      borderRadius: 0,
    },
    ready: {
      backgroundColor: theme.palette.grey.A100,
    },
    running: {
      backgroundColor: theme.palette.success.light,
    },
    paused: {
      backgroundColor: theme.palette.secondary.light,
    },
    stopped: {
      backgroundColor: theme.palette.info.light,
    },
  })
)
export function ExperimentStateReady() {
  const classes = useStyles()
  return <Chip size={"small"} label="Ready" className={`${classes.status} ${classes.ready}`} />
}
export function ExperimentStateRunning() {
  const classes = useStyles()
  return <Chip size={"small"} label="Running" className={`${classes.status} ${classes.running}`} />
}
export function ExperimentStatePaused() {
  const classes = useStyles()
  return <Chip size={"small"} label="Paused" className={`${classes.status} ${classes.paused}`} />
}
export function ExperimentStateStopped() {
  const classes = useStyles()
  return <Chip size={"small"} label="STOPPED" className={`${classes.status} ${classes.stopped}`} />
}
