import React from "react"
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  createStyles,
  Divider,
  Drawer,
  FormControlLabel,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Radio,
  RadioGroup,
  Typography,
} from "@material-ui/core"
import { ExpandMore as ExpandMoreIcon } from "@material-ui/icons"
import pages, { RoutingComponent } from "../pages"
import { Link } from "react-router-dom"
import { makeStyles } from "@material-ui/core/styles"
import { useRecoilState } from "recoil/dist"
import { envProfileState, EnvType } from "../states/EnvProfile"

const drawerWidth = 240

const useStyles = makeStyles((theme) =>
  createStyles({
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
    },
    drawerHeader: {
      display: "flex",
      alignItems: "center",
      padding: theme.spacing(0, 1),
      justifyContent: "flex-end",
    },
    drawerPaper: {
      width: drawerWidth,
    },
    drawerContainer: {
      overflow: "auto",
    },
    sectionDesktop: {
      [theme.breakpoints.up("md")]: {
        display: "flex",
      },
    },
    accordionDevelopment: {
      backgroundColor: theme.palette.background.default,
    },
    accordionProduction: {
      backgroundColor: theme.palette.primary.dark,
      color: theme.palette.common.white,
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
    },
  })
)

export default function LeftSideBar() {
  const classes = useStyles()
  const [envProfile, setEnvProfile] = useRecoilState(envProfileState)

  const isActive = (value: string): boolean => {
    return window.location.pathname.includes(value)
  }

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    localStorage.setItem("envProfile", "production")
    setEnvProfile(event.target.value as EnvType)
  }

  // @ts-ignore
  return (
    <Drawer
      className={classes.drawer}
      variant="permanent"
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <Accordion>
        <AccordionSummary
          className={`${envProfile === "production" ? classes.accordionProduction : classes.accordionDevelopment}`}
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography variant="body1">
            Default Workspace
            <Typography variant="body2">{envProfile}</Typography>
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <RadioGroup aria-label="profile" name="profile" value={envProfile} onChange={handleChange}>
            <FormControlLabel value="production" control={<Radio />} label="Production" />
            <FormControlLabel value="development" control={<Radio />} label="Development" />
          </RadioGroup>
        </AccordionDetails>
      </Accordion>
      <div className={classes.drawerContainer}>
        <List>
          <SideBarListItem
            selected={isActive(pages.experiment.path)}
            to={`/experiments`}
            routingComponent={pages.experiment}
          />
          <SideBarListItem selected={isActive(pages.event.path)} to={`/events`} routingComponent={pages.event} />
          <br />
          <Divider />
          <br />
          <SideBarListItem selected={isActive(pages.setting.path)} to={`/settings`} routingComponent={pages.setting} />
          <SideBarListItem
            selected={isActive(pages.tutorial.path)}
            to={`/tutorials`}
            routingComponent={pages.tutorial}
          />
          <SideBarListItem
            selected={isActive(pages.developers.path)}
            to={`/developers`}
            routingComponent={pages.developers}
          />
        </List>
      </div>
    </Drawer>
  )
}

function SideBarListItem({
  routingComponent,
  to,
  disabled,
  selected,
}: {
  routingComponent: RoutingComponent<any>
  to: string
  disabled?: boolean
  selected?: boolean
}) {
  const Icon = routingComponent.componentIcon

  return (
    <ListItem selected={selected} disabled={disabled} button component={Link} to={to}>
      <ListItemIcon>
        <Icon />
      </ListItemIcon>
      <ListItemText primary={routingComponent.text} />
    </ListItem>
  )
}
