import React, { ChangeEvent, useState } from "react"
import {
  Button,
  Grid,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
  Input,
  IconButton,
  Chip,
} from "@material-ui/core"
import { createStyles, makeStyles } from "@material-ui/core/styles"
import { RouteComponentProps } from "react-router-dom"
import EmptyDivider from "../../components/Divider/EmptyDivider"
import { AddCircleOutline, RemoveCircleOutline } from "@material-ui/icons"
import ExperimentVariation from "./ExperimentVariation"

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    divider: {
      marginBottom: "16px",
    },
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary,
    },
    buttonGroup: {
      textAlign: "right",
    },
    button: {
      margin: theme.spacing(1),
    },
    margin: {
      margin: theme.spacing(1),
    },
    marginR: {
      marginLeft: theme.spacing(0.5),
      marginRight: theme.spacing(0.5),
    },
    textField: {
      width: "25ch",
    },
    table: {
      minWidth: "650px",
    },
    gray: {
      color: theme.palette.grey.A200,
    },
    distributionInput: {
      textAlign: "right",
    },
  })
)
const nameMaxLength = 128
const descriptionMaxLength = 512
export default function ExperimentsCreation(props: RouteComponentProps) {
  const classes = useStyles()

  const [name, setName] = useState("")
  const [description, setDescription] = useState("")
  const goBack = () => {
    // @ts-ignore
    props.history.goBack()
  }
  const create = () => {
    goBack()
  }
  const changeName = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value
    if (nameMaxLength < value.length) return
    setName(value)
  }
  const changeDescription = (event: ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value
    if (descriptionMaxLength < value.length) return
    setDescription(value)
  }

  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs={12}>
          <Typography variant={"h6"}>Create new A/B Test</Typography>
          <Typography variant={"caption"}>
            <Chip size={"small"} className={classes.marginR} label="Production" variant={"outlined"} color="primary" />
            <Chip size={"small"} className={classes.marginR} label="Development" variant={"outlined"} color="default" />
          </Typography>
        </Grid>
      </Grid>
      <div className={classes.divider} />
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Grid container spacing={3}>
              <Grid item xs={9}>
                <TextField
                  label="Name"
                  style={{ margin: 8 }}
                  helperText={`${name.length}/128`}
                  fullWidth
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  value={name}
                  required={true}
                  autoFocus={true}
                  onChange={changeName}
                />
                <TextField
                  label="Description (Optional)"
                  style={{ margin: 8 }}
                  helperText={`${description.length}/512`}
                  fullWidth
                  margin="normal"
                  InputLabelProps={{
                    shrink: true,
                  }}
                  value={description}
                  onChange={changeDescription}
                />
              </Grid>
            </Grid>
            <EmptyDivider />
            <Grid container spacing={3}>
              <Grid item xs={10}>
                <ExperimentVariation />
              </Grid>
            </Grid>
            <EmptyDivider />
            <div className={classes.buttonGroup}>
              <Button variant="contained" color="default" className={classes.button} onClick={goBack}>
                Cancel
              </Button>
              <Button variant="contained" color="primary" className={classes.button} onClick={create}>
                Create
              </Button>
            </div>
          </Paper>
        </Grid>
      </Grid>
    </div>
  )
}
